import React, { useEffect, useState } from "react";
import { Container, Table } from "reactstrap";
import { Repe } from "../Repetition/Repe";

export const Serie = ({ exerciseName, numReps }) => {
  const [name, setName] = useState('');
  const [comments, setComments] = useState(0)
  const [repes, setRepes] = useState([])
  const [isEditable, setIsEditable] = useState(false)

  useEffect(() => {
    setName(exerciseName);
    setRepes(Array.from(Array(numReps).keys()));
  }, [name, repes]);

  return (
    <Container>
    
    <Table>
    <thead>
          <tr>
            <th>#</th>
            <th>#repetitions</th>
            <th>weight</th>
          </tr>
        </thead>
    <tbody>
      {repes.map( (r) => (<Repe id={r} pNum={3} pWeigth={100} isEditable={ isEditable }></Repe>))}
    </tbody>
    </Table>
    </Container>
  );
};